.footer {
background: rgba(0, 255, 255, .2);
    height: 80px;
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    bottom: 0;
    color: black;
    z-index: 999;
    font-family: 'Audiowide', sans-serif;
}

.footer-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 100%; 
}

.footer-name {
    font-family: "Audiowide", sans-serif;
}

@media screen and (max-width: 960px) {
    .footer {
        text-align: center;
    }
}