.bg-video {
  /*code extended video to full screen*/
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.hero-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
  font-family: "Oooh Baby";
}

.intro {
  position: relative;
  top: 0;
  font-size: 100px;
  color: black;
  padding-top: 150px;
  text-align: center;
}

.date {
  position: relative;
  top: 0;
  color: black;
  padding-top: 100px;
  font-size: 60px;
  text-align: center;
}

.time {
  position: relative;
  font-size: 30px;
  color: black;
  padding-top: 30px;
  font-weight: 600;
}

.info {
  position: relative;
  font-size: 40px;
  padding-top: 100px;
  font-weight: 600;
  margin: auto 240px;
  line-height: 4rem;
}

@media screen and (max-width: 960px) {
  video {
    /*code extended video to full screen*/
    object-fit: cover;
    object-position: right;
  }

  .hero-container > h1 {
    font-size: 80px;
    padding: 150px 0 150px 0;
  }

  .date {
    font-size: 60px;
    padding-top: 20px;
  }

  .intro {
    margin: 0 10px;
  }

  .info {
    font-size: 35px;
    margin: auto 20px;
  }

  .time {
    padding-bottom: 100px;
  }
}

@media screen and (min-width: 961px) and (max-width: 1300px) {
  video {
    /*code extended video to full screen*/
    object-fit: cover;
    object-position: right;
  }

  .hero-container > h1 {
    font-size: 100px;
    padding-top: 200px;
  }
}
