.navbar {
    background: rgba(0,255,255,.2);
    display: flex;
    height: 80px;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
    font-family: 'Open Sans';
}

.navbar-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
}

.nav-item {
    height: 80px;
  }
  
  .nav-links {
    color: black;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    font-size: 26px;
    font-weight: 600;
  }
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 90vw;
    justify-content: end;
    margin-right: 2rem;
  }

  .fa-bars {
    color: #333;
    position: relative;
  }

  .nav-links-mobile {
    display: none;
  }

  .menu-icon {
    display: none;
  }

  .nav-links:hover {
    color: #fff;
    transition: all 0.2s ease-out;
    
  }

/*Responsive Navbar*/

  @media screen and (max-width: 960px) {

    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: inline-block;
      width: 100%;
      height: 65vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
      overflow-y: scroll;
    }
  
    .nav-menu.active {
      background: rgb(203, 255, 255);
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color:#fff;
      color: black;
      border-radius: 0;
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #333;
      font-size: 2rem;
    }
  }