.gallery-container {
  background: url(/public/images/blueballoon.jpg);
  background-size: cover;
  background-position: right;
  background-repeat: no-repeat;
  font-family: "Open Sans";
}

.gallery {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 50px;
  max-width: 1500px;
  margin: 0 auto;
  padding-bottom: 100px;
}

.gallery-info {
  padding-bottom: 100px;
}

.gallery-title {
  padding-top: 130px;
  font-size: 70px;
  color: black;
  text-align: center;
}

.gallery-text {
  margin: 80px 250px;
  font-size: 35px;
  color: black;
  line-height: 4rem;
  font-weight: 600;
}

.video {
  padding: 20px 20px 20px 20px;
  width: 400px;
  height: 700px;
  border: 3px solid black;
  border-radius: 16px;
  background: #fff;
}

#reveal {
  padding-top: 20px;
}

.video-caption {
  text-align: center;
  font-size: 28px;
  font-weight: 600;
  padding-top: 20px;
  padding-bottom: 30px;
  color: black;
}

@media screen and (max-width: 960px) {
  .gallery-container {
    background-position: center;
  }

  .gallery {
    gap: 30px;
  }

  .gallery-text {
    margin: 50px 25px;
    font-size: 35px;
  }

  .gallery-title {
    text-align: center;
  }

  .video {
    width: 350px;
    height: 650px;
    padding-top: 20px;
  }
  .video-caption {
    font-size: 22px;
  }
}

@media screen and (min-width: 961px) and (max-width: 1440px) {
  .gallery-text {
    margin: 50px 100px;
    font-size: 35px;
  }
}
