span {
    color: white;
  }

.cards{
    position: relative;
    max-width: 100%;
    max-height: 100%;
    padding: 4rem;
    margin-top: 50px;
    background: transparent;
    font-family: 'Open Sans', sans-serif;
}

.cards__container { /*display setup for items in container*/
    display:flex;
    max-width: 1400px;
    width: 90%;
    margin: 0 auto;
}

.cards__wrapper {
    position: relative;
    margin: 50px 0 45px;
}

.cards__items {
    margin-bottom: 24px;
}

.cards__item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
    background: rgba(0,255,255,.2);
}

.cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
}

.card__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
}

.fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
}

.card__item__pic-wrap::after {
    content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 24px;
  font-weight: 700;
  color: #fff;
  background: rgba(56, 125, 255, 0.3);
  box-sizing: border-box;
  border-radius: 4px;
}

.card__item__img{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
}

.card__item__img:hover{
    transform: scale(1.1);
}

.cards__item__info {
    padding: 20px 30px 30px;
}

.cards__item__text {
    color: black;
    font-size: 20px;
    line-height: 24px;
    font-family: 'Open Sans';
}


@media screen and (min-width: 960px) {
    .cards__items {
        display: flex;
    }    
}

@media screen and (max-width: 960px) {
    .cards {
        padding: 1rem;
    }
    
    .cards__item {
        margin-bottom: 2rem;
    }  
    
    .cards__item__text {
        font-size: 18px;
    }

    .card__item__pic-wrap::after {
      font-size: 14px;
    }
}