em {
  color: blue;
}

.location-container {
  font-family: "Open Sans";
  display: flex;
  flex-direction: column;
  background: url(/public/images/bear.jpeg);
  background-position: center;
  background-size: cover;
}

.location-title {
  font-size: 70px;
  color: black;
  padding-top: 130px;
  text-align: center;
}

.location-info {
  padding-top: 100px;
  line-height: 3rem;
  margin: 0 300px;
}

.location-text {
  font-size: 35px;
  font-weight: 600;
  color: black;
  line-height: 4rem;
}

.map-container {
  display: flex;
  justify-content: center;
  padding: 100px 0 100px 0;
}

@media screen and (max-width: 960px) {
  .location-info {
    padding-top: 100px;
    line-height: 3rem;
    margin: 0 20px;
  }

  .location-title {
    text-align: center;
  }
}
